  src: url("../fonts/Hind-SemiBold.ttf");
}

@font-face {
  font-family: 'Hind-Bold';
  src: url("../fonts/Hind-Bold.ttf");
}

@font-face {
  font-family: 'Montserrat-Bold';
  src: url("../fonts/Montserrat-Bold.ttf");
}

@font-face {
  font-family: 'Hind-Light';
  src: url("../fonts/Hind-Light.ttf");
}

