  font-family: 'Hind-Regular';
  src: url("../fonts/Hind-Regular.ttf");
}

@font-face {
  font-family: 'OpenSans-Light';
  src: url("../fonts/OpenSans-Light.ttf");
}

body {
  margin: 0;
  background-color: #f9f9fb;
  position: relative;
}

header {
  width: 100%;
  height: 345px;
  background-color: #212429;

  .wrapper {
    max-width: 1530px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 auto;

    .menu {

      img {
        display: block;
        height: 16px;
        padding: 20px;
        border-left: 1px solid #3c3c3c;
        border-right: 1px solid #3c3c3c;
      }
    }

    .logo {
      position: relative;
      left: 130px;

      img {
        float: left;
        margin-top: 22px;
      }

      h2 {
        margin: 15px 0 0 5px;
        float: left;
        font: 22.15px 'Hind-SemiBold', sans-serif;
        color: #efeff0;
        text-transform: uppercase;
        letter-spacing: 4px;
      }
    }

    .social {
      display: flex;
      flex-direction: row;
      position: relative;
      left: 5px;

      img {
        display: block;
        height: 16px;
        padding: 20px;
        border-left: 1px solid #3c3c3c;

        &:last-child {
          border-right: 1px solid #3c3c3c;
        }
      }
    }
  }
}

.banner {
  position: absolute;
  display: flex;
  top: 56px;
  left: 50%;
  width: 1530px;
  height: 580px;
  margin-left: -765px;
  flex-direction: row;
  justify-content: space-around;
  background: url(../img/bannerBG.png) center center;
  background-size: cover;

  .banner_text {
    margin: 230px 40% 0 0;

    h3 {
      font: 18px 'Hind-SemiBold', sans-serif;
      margin: 0;
      letter-spacing: 4px;
      color: #ffffff;
    }

    h4 {
      font: 40px 'Hind-Bold', sans-serif;
      letter-spacing: 8px;
      text-transform: uppercase;
      color: #ffffff;
      margin: 10px 0 0;
    }
  }

  .banner_slider {
    display: flex;
    flex-direction: row;
    margin: 160px 0 0 0;

    .box {
      display: flex;
      flex-direction: column;
      margin: 100px 22px 0 0;

      img {
        display: block;
        width: 8px;
        height: 8px;
        margin-bottom: 10px;
      }
    }

    .arrow {
      display: flex;
      flex-direction: column;
      width: 35px;
      height: 200px;

      img {
        display: block;
        width: 12px;
        height: 26px;

        &:first-child {
          margin-bottom: 17px;
        }

        &:last-child {
          margin-top: 17px;
          position: relative;
          top: 35%;
        }
      }

      h6 {
        text-transform: uppercase;
        transform: rotate(-90deg);
        color: #ffffff;
        font: 14px 'Hind-SemiBold', sans-serif;
        letter-spacing: 3px;
        white-space: nowrap;
        position: relative;
        top: 50%;
        left: -22%
      }
    }
  }
}


.newsletter {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #f7f7f7;
  border: 1px solid #f5f5f7;
  padding-top: 315px;
  margin: 0 auto;
  width: 1530px;

  .name {

    h4 {
      font: 20px 'Montserrat-Bold', sans-serif;
      letter-spacing: 3px;
      text-transform: uppercase;
      color: #212121;
      margin: 0;
    }
  }

  .inputs {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .formName,
    .formMail {

      input {
        width: 420px;
        height: 50px;
        background-color: #ffffff;
        border: 1px solid #f3f3f3;
        padding: 0 0 0 30px;
        font: 15px 'Hind-Light', sans-serif;
        text-transform: uppercase;
        letter-spacing: 3px;
        color: #999999;
        margin-right: 20px;

        &:first-child {
        }
      }
    }

    .formName {
      margin: 0 0 22px 0;
    }
  }

  .button {

    button {
      width: 280px;
      height: 50px;
      background-color: #212121;
      border: none;
      font: 14px 'Hind-Medium', sans-serif;
      color: #ffffff;
      text-transform: uppercase;
      letter-spacing: 3px;
      padding: 0;
    }
  }
}

.content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 1530px;
  height: 100%;
  margin: 80px auto 60px;

  article {
    width: 360px;
    height: 100%;
  }

  .publication-white {
    position: relative;
    background-color: #ffffff;
    border: 1px solid #f1f1f3;

    .icon {
      position: absolute;
      top: -17.5px;
      left: 50%;
      margin-left: -20px;
    }

    h5 {
      font: 14px 'Hind-Regular', sans-serif;
      color: #999999;
      text-transform: uppercase;
      letter-spacing: 2px;
      text-align: center;
      margin: 0 auto 20px;
    }

    .topText {
      margin-top: 55px;
    }

    h3 {
      font: 20px 'Montserrat-Bold', sans-serif;
      text-transform: uppercase;
      color: #212121;
      text-align: center;
      margin: 0 auto 20px;
    }

    p {
      font: 15px 'Hind-Light', sans-serif;
      color: #464646;
      text-align: center;
      line-height: 1.8;
      letter-spacing: 0.5px;
    }

    button {
      width: 126px;
      height: 30px;
      display: block;
      margin: 40px auto;
      font: 14px 'Hind-Medium', sans-serif;
      text-transform: uppercase;
      color: #212121;
      letter-spacing: 2px;
      border: none;
      border-left: 1px solid #d7d7d7;
      border-right: 1px solid #d7d7d7;
      background-color: transparent;
    }

    .bottom {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      border-top: 1px solid #f1f1f3;

      .comment {
        margin: 24px 20px 0 28px;
        display: flex;
        flex-direction: row;

        img {
          position: relative;
          width: 16px;
          height: 12px;
          margin-right: 9px;
        }

        h6 {
          font: 14px 'Hind-Regular', sans-serif;
          color: #999999;
          text-transform: uppercase;
          letter-spacing: 2px;
          margin: -2px 0 0;
        }
      }

      .mini-social {
        margin: 24px 10px 20px 0;

        img {
          position: relative;
          float: left;
          margin-right: 20px;
        }
      }
    }
  }

  .one {
  }

  .dark {
    background: #f4f4f4;

    .two {
      margin: 250px 0 0;
    }
  }

  .three {
  }

  .light {
    height: 510px;
    background-color: #ffffff;
    border: 1px solid #f1f1f3;
  }

  .next {
    margin-top: -40px;
  }

  .publication-black {
    position: relative;
    background-color: #212429;

    .icon {
      position: absolute;
      top: -17.5px;
      left: 50%;
      margin-left: -20px;
    }
  }

  .four {
    margin-top: 45px;

    p {
      font: 17px 'Hind-Light', sans-serif;
      color: #ffffff;
      letter-spacing: 0.5px;
      line-height: 1.6;
      margin: 58px 0 40px;
      text-align: center;
    }

    h6 {
      font: 16px 'Hind-Regular', sans-serif;
      color: #999999;
      letter-spacing: 2px;
      text-transform: uppercase;
      text-align: center;
    }
  }

  .grid {
    overflow: hidden;
    display: flex;
    flex-direction: column;
    background-color: white;
    margin-top: -120px;
    box-shadow: 0 0 20px 6px #d0d0d2;

    .grid_top,
    .grid_down {
      display: flex;
      flex-direction: row;

      section {
        width: 179px;
        height: 125px;

        &:first-child {
          border-right: 1px solid #fafafa;
        }
      }
    }

    .grid_top {
      section {
        border-bottom: 1px solid #fafafa;
      }
    }

    .five {

      button {
        border: none;
        color: #999999;

        &:before {
          content: " ";
          display: block;
          margin: 0 auto 5px;
          width: 30px;
          height: 1px;
          background-color: #d7d7d7;
        }

        &:after {
          content: " ";
          display: block;
          margin: 5px auto 0;
          width: 30px;
          height: 1px;
          background-color: #d7d7d7;
        }
      }
    }
  }

  .six {
    margin: -20px 0 50px;
  }

  .seven {
    margin-top: -30px;
    background: url("../img/layer.png") center center;
    background-size: cover;

    .topText {
      margin-top: 40px;
    }
  }

  .videoPost {
    position: relative;
    background: #f4f4f4;
    margin-top: -112px;

    .videoPost_image {
      position: absolute;
      top: -17.5px;
      left: 50%;
      margin-left: -20px;
    }

    .eight {
      margin-top: 225px;
    }
  }

  .nine {
    margin-top: -43px;

    .nine_top {
      width: inherit;
      position: relative;
      background-color: #212429;

      p {
        font: 17px 'Hind-Light', sans-serif;
        color: #ffffff;
        text-align: center;
        padding: 63px 0;
      }

      .icon {
        position: absolute;
        top: -17.5px;
        left: 50%;
        margin-left: -20px;
      }
    }

    .nine_down {
      width: inherit;
      height: 287px;
      position: relative;
      background-color: #212429;
      margin-top: 48px;

      .icon {
        position: absolute;
        top: -17.5px;
        left: 50%;
        margin-left: -20px;
      }
    }
  }

  .ten {

    .icon {
      position: absolute;
      top: -17.5px;
      left: 50%;
      margin-left: -20px;
    }

    .ellipse {
      display: block;
      margin: 30px auto 160px;
    }
  }
}

footer {
  width: 100%;
  height: 100%;
  background-color: #212429;

  .wrapper {
    max-width: 1530px;
    width: 100%;
    margin: 0 auto 60px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    h2 {
      margin: 60px 0 40px 0;
      font: 16px 'Montserrat-Bold', sans-serif;
      text-transform: uppercase;
      color: #ffffff;
    }

    p {
      font: 15px 'Hind-Light', sans-serif;
      color: #ffffff;
      letter-spacing: 1.5px;
      margin: 0 0 10px;
    }

    .twit {

      .twit_post {
        display: flex;
        flex-direction: row;
        margin-top: 35px;

        h6 {
          font: 15px 'Hind-Light', sans-serif;
          color: #ffffff;
          letter-spacing: 1.5px;
          margin: 0;
        }

        img {
          display: block;
          width: 17px;
          height: 17px;
          margin: 2px 5px 0 0;
        }
      }

      p {
        margin: 10px 0 0 25px;
      }
    }

    .inst {

      .figure {
        display: flex;
        flex-direction: row;

        .square {
          display: flex;
          flex-direction: column;


          section {
            width: 120px;
            height: 100px;
            background-color: #f9f9fb;
            margin: 0 2px 2px 0;
          }
        }
      }
    }

    .comm {

      h3,
      h4 {
        margin: 0;
        font: 14px 'OpenSans-Light', sans-serif;
        color: #ffffff;
      }

      h3 {
        margin-bottom: 15px;
      }

      h4 {
        line-height: 1.6;
      }

      .comm_top {
        border-bottom: 1px solid #65676a;

        h4 {
          padding-bottom: 28px;
        }
      }

      .comm_down {

        h3 {
          margin-top: 28px;
        }
      }
    }
  }
}

.copyright {
  width: 100%;
  height: auto;
  background-color: #17191c;

  p {
    font: 14px 'Hind-Light', sans-serif;
    color: #747577;
    padding: 10px 0;
    text-align: center;
    margin: 0;
  }
}


